<template>
  <div class="newemployee container">
    <h1>New Employee</h1>
    <form @submit.prevent='saveEmployee'>
      <div class="input-field">
        <i class="material-icons prefix">account_circle</i>
        <input type="text" id="disabled" disabled/>
        <label for="disabled">{{id}}</label>
      </div>
      <div class="input-field">
        <i class="material-icons prefix">mode_edit</i>
        <textarea id="text-info" class="materialize-textarea" v-model="textinfo"></textarea>
        <label for="text-info">massage</label>
      </div>
      <div class="input-field">
        <input type="text" id="member-name" v-model="name"/>
        <label for="member-name">name</label>
      </div>
      <div class="input-field">
        <input type="text" id="member-job" v-model="job"/>
        <label for="member-job">job</label>
      </div>
      <button type="submit" class="waves-effect waves-light btn"><i class="material-icons left">cloud_upload</i>submit</button>
      <router-link to='/' class="waves-effect waves-light btn red"><i class="material-icons left">cancel</i>cancel</router-link>
    </form>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'NewEmployee',
  data () {
    return {
      id: null,
      name: null,
      textinfo: null,
      job: null
    }
  },
  created () {
    const settings = {timestampsInSnapshots: true}
    db.settings(settings)
    db.collection('employeement').orderBy('id').get().then(querySnapshot => {
      let userNum = 0
      const idDataArray = []
      querySnapshot.forEach(doc => {
        userNum += 1
        const idData = {
          'id': doc.data().id
        }
        idDataArray.push(idData)
      })
      this.id = idDataArray[userNum - 1].id + 1
    })
  },
  methods: {
    saveEmployee () {
      const settings = {timestampsInSnapshots: true}
      db.settings(settings)
      const dataRef = db.collection('employeement')
      dataRef.add({
        id: this.id,
        name: this.name,
        textinfo: this.textinfo,
        job: this.job
      }).then(docRef => this.$router.push('/')).catch(error => console.log(error))
    }
  }
}
</script>
<style lang="scss" scoped>
  $btnColor : #fff;
  .btn-gray{
    &:hover{color:#fff;}
    background:rgb(69, 69, 69); color:#fff;
  }
</style>



