<template>
  <div class="viewemployee container">
    <h1>View Employee</h1>
    <p>Name : {{name}}<p>
    <p>Job : {{job}}<p>
    <p>Text Information :</p>
    <textarea class="form-control" rows="3" v-model="textinfo"> </textarea>
    <router-link to='/' class="waves-effect waves-light btn"><i class="material-icons left">format_list_bulleted</i>cancel</router-link>
    <button @click="deleteEmployee" class="waves-effect waves-light btn red"><i class="material-icons left">delete</i>delete</button>
  </div>
</template>

<script>
import db from './firebaseInit'

export default {
  name: 'view-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      textinfo: null,
      job: null
    }
  },
  created () {
    this.fetchData()
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      const settings = {timestampsInSnapshots: true}
      db.settings(settings)
      const emNum = parseInt(this.$route.params.employee_id)
      const dataRef = db.collection('employeement').where('id', '==', emNum)
      dataRef.get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.employee_id = doc.data().id
          this.name = doc.data().name
          this.textinfo = doc.data().textinfo
          this.job = doc.data().job
        })
      }).catch(function (error) {
        console.log('Error getting documents: ', error)
      })
    },
    deleteEmployee () {
      if (confirm('are you sure?')) {
        const settings = {timestampsInSnapshots: true}
        db.settings(settings)
        const emNum = parseInt(this.$route.params.employee_id)
        const dataRef = db.collection('employeement').where('id', '==', emNum)
        dataRef.get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>
<style lang="scss">
</style>



