<template>
  <div class="dashboard container">
    <h3>Massage List</h3>
    <table>
      <thead>
        <tr>
          <th>#</th>
          <th>Name</th>
          <th>Massage</th>
          <th>Position</th>
          <th>Detail View</th>
        </tr>
      </thead>

      <tbody>
        <tr>
          <tr v-for="item in employees" v-bind:key="item.employee_id">
            <td><span>{{item.id}}</span></td>
            <td>{{item.name}}</td>
            <td>{{item.textinfo}}</td>
            <td>{{item.job}}</td>
            <td><router-link v-bind:to="{ name: 'view-employee', params: { employee_id: item.employee_id }}"><i class="material-icons center">remove_red_eye</i></router-link></td>
        </tr>
      </tbody>
    </table>
    <button type="button" class="waves-effect waves-light btn right"><i class="material-icons left">cloud</i><router-link to="/new">wirte</router-link></button>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      employees: []
    }
  },
  created () {
    const settings = {timestampsInSnapshots: true}
    db.settings(settings)
    db.collection('employeement').orderBy('id').get().then(querySnapshot => {
      let idNum = 0
      querySnapshot.forEach(doc => {
        idNum += 1
        const data = {
          'id': idNum,
          'employee_id': doc.data().id,
          'name': doc.data().name,
          'textinfo': doc.data().textinfo,
          'job': doc.data().job
        }
        this.employees.push(data)
      })
    })
  }
}
</script>

<style lang="scss">
.dashboard table th,.dashboard table td,.dashboard table{text-align:center;}
.dashboard table td span{margin:0 auto;}
</style>



